$class: 'about';

.#{$class} {
  // set some whitespace between content and edges
  padding: 5vw;
}

%avatar-common {
  // make block shape of the elemant a circle
  border-radius: 50%;

  // use block for margin: auto better positioning
  display: block;

}

.#{$class}-section-title{
  @extend %section-title;
}

.#{$class}-avatar {
  // apply circle shape and display block
  @extend %avatar-common;

  // stretch as much as the parent container
  width: 100%;

  // set the border to white so it blends with the background
  border: 0.5rem solid $color-white;
}

.#{$class}-avatar-wrapper {
  // apply circle shape and display block
  @extend %avatar-common;

  // set the max width to 80%
  max-width: 80%;
  // and combine it with margin auto to position
  // it to center (like text-align: center)
  margin: 1rem auto;

  // and set the color to contrast with the background
  border:0.5rem solid $color-gumbo;
}

.#{$class}-title {
  // set the color to contrast the background
  color: $color-gumbo;
  // and a slightly bigger font size value
  font-size: 2.5rem;
  // and align it to center
  text-align: center;
  // and increase the whitw space on top and bottom
  padding: 2rem 0 1rem;
}

.#{$class}-paragraph {
  // set the font size to be easy to read
  font-size: 1.2rem;
  // and increase the spacing between two lines
  line-height: 1.7;
  // and set the text to be displayed from left margin
  // all the way to the right margin
  text-align: justify;
  // and increase the top and bottom white space
  padding: 0.7rem 0;
  // and indet the first line
  text-indent: 2rem;
}
